* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.main-container {
  height: 200vh;
  padding: 35px;
  background-color: #9ba9eb;
}
.app-logo {
  height: 65px;
}

.add-details {
  color: #fff;
  padding: 25px;
  border-radius: 6px;
  background-color: #454f84;
}
.add-btn {
  background-color: #0b69ff;
  color: #fff;
  border: none;
  height: 30px;
  width: 65px;
  border-radius: 6px;
}
.input-holder {
  background-color: #fff;
  margin: 14px;
  display: flex;
  height: 40px;
  border: 1px solid grey;
}
.input-element {
  border: none;
  outline: none;
}
.input-image {
  height: 40px;
  border: 1px solid grey;
}
.btn-element {
  display: flex;
  justify-content: flex-end;
}
@media screen and (max-width: 568px) {
  .sub-div1-image1 {
    display: none;
  }
  .sub-div1-image2 {
    height: 250px;
  }
  .main-container {
    height: 150vh;
    width: 100vw;
  }
  .no-password-image {
    height: 55px;
    width: 75px;
  }
}
@media screen and (min-width: 767px) {
  .sub-div1 {
    display: flex;
    border-radius: 6px;
    justify-content: space-around;
    background-color: #5763a5;
    padding: 25px;
  }
  .sub-div1-image2 {
    display: none;
  }
  .sub-div1-image1 {
    height: 390px;
    order: 1;
  }
}
.sub-div2 {
  background-color: #454f84;
  padding: 30px;
  margin-top: 20px;
  border-radius: 6px;
}
.first-div {
  display: flex;
  justify-content: space-between;
}
.your-password {
  display: flex;
}
.colored-text {
  border: 1px solid #fff;
  border-radius: 25px;
  width: 25px;
  height: 30px;
  color: #fff;
  text-align: center;
}
.search-holder {
  background-color: #fff;
  height: 39px;
  width: 240px;
  display: flex;
}
.no-password {
  color: #fff;
}
.no-password-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.no-password-image {
  height: 350px;
  width: 450px;
}
.initial {
  font-size: 16px;
  color: #fff;
  padding: 8px;
  background-color: greenyellow;
  border-radius: 25px;
  text-align: center;
  height: 45px;
  width: 45px;
}
.item-list {
  border: 1px solid #fff;
  padding: 25px;
  width: 380px;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
}
.star-image {
  width: 140px;
}
.delete-btn-element {
  background-color: transparent;
  border: none;
  outline: none;
}
.delete-image {
  background-color: transparent;
  height: 35px;
}
